import React, { useState } from 'react';
import { Sword, Trophy, Trash2 } from 'lucide-react';
import clsx from 'clsx';
import { Boss } from '../../types';
import { useBoss } from '../../context';
import { BossAttackModal } from './BossAttackModal';
import { ConfirmModal } from '../common/ConfirmModal';

interface BossItemProps {
  boss: Boss;
  onEdit: (boss: Boss) => void;
}

export const BossItem: React.FC<BossItemProps> = ({ boss, onEdit }) => {
  const { deleteBoss } = useBoss();
  const [isAttackModalOpen, setIsAttackModalOpen] = useState(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);

  const percentage = Math.round((boss.currentHealth / boss.maxHealth) * 100);
  const isDefeated = boss.status === 'defeated';

  const handleDeleteClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    setIsDeleteModalOpen(true);
  };

  const handleDeleteConfirm = () => {
    deleteBoss(boss.id);
  };

  return (
    <>
      <div
        className={clsx(
          'relative overflow-hidden bg-white rounded-xl shadow-sm border transition-all hover:shadow-md hover:-translate-y-1 cursor-pointer flex flex-col group',
          // Adjusted aspect ratio to be taller to fit more content
          'aspect-[3/5]',
          isDefeated ? 'border-yellow-200 bg-yellow-50' : 'border-gray-100'
        )}
        onClick={() => setIsAttackModalOpen(true)}
      >
         {/* Status Badge - Moved to top left to make room for delete button */}
        {isDefeated && (
          <div className="absolute top-2 left-2 z-10">
            <Trophy className="text-yellow-500 drop-shadow-sm" size={24} />
          </div>
        )}

        {/* Delete Button - Moved to top right and increased size */}
        <button
          onClick={handleDeleteClick}
          className="absolute top-2 right-2 z-10 p-2 text-gray-300 hover:text-red-500 hover:bg-red-50 rounded-full transition-colors"
        >
          <Trash2 size={20} />
        </button>

        {/* Main Content Area */}
        <div className="flex-1 flex flex-col p-4 w-full h-full">
          
          {/* Avatar Area */}
          <div className="flex justify-center mt-4 mb-4 shrink-0">
             <div className={clsx(
              "w-24 h-24 rounded-2xl flex items-center justify-center font-bold text-white shadow-md overflow-hidden",
              isDefeated ? "bg-yellow-200" : "bg-red-500"
            )}>
              {boss.avatar ? (
                <img src={boss.avatar} alt={boss.name} className="w-full h-full object-cover" />
              ) : (
                <span className="text-4xl">{boss.name.charAt(0).toUpperCase()}</span>
              )}
            </div>
          </div>

          {/* Info Area */}
          <div className="flex-1 flex flex-col items-center text-center min-h-0 space-y-3">
             {/* Name */}
             <h3 className="text-xl font-bold text-gray-900 w-full px-1 leading-tight shrink-0">
               {boss.name}
             </h3>

             {/* Description - Moved up and allowed to expand */}
             <p className="text-sm text-gray-500 w-full px-1 leading-relaxed">
               {boss.description}
             </p>

             {/* Difficulty - Moved down */}
             <div className="flex items-center space-x-1 mt-auto pt-2 shrink-0">
              {[...Array(5)].map((_, i) => (
                <Sword
                  key={i}
                  size={16}
                  className={i < boss.difficulty ? 'text-red-500' : 'text-gray-200'}
                  fill={i < boss.difficulty ? 'currentColor' : 'none'}
                />
              ))}
            </div>
          </div>

          {/* Bottom Area - HP Bar */}
          <div className="mt-auto pt-3 w-full shrink-0">
            <div className="flex justify-between text-[10px] font-medium text-gray-500 mb-1 px-1">
              <span>HP</span>
              <span>{percentage}%</span>
            </div>
            <div className="h-2 bg-gray-100 rounded-full overflow-hidden border border-gray-100">
              <div
                className={clsx(
                  'h-full transition-all duration-500 ease-out',
                  isDefeated ? 'bg-gray-300' : percentage < 30 ? 'bg-red-500' : 'bg-blue-500'
                )}
                style={{ width: `${percentage}%` }}
              />
            </div>
          </div>
        </div>
      </div>

      <BossAttackModal 
        isOpen={isAttackModalOpen}
        onClose={() => setIsAttackModalOpen(false)}
        boss={boss}
        onEdit={() => onEdit(boss)}
        onDelete={() => deleteBoss(boss.id)}
      />

      <ConfirmModal
        isOpen={isDeleteModalOpen}
        onClose={() => setIsDeleteModalOpen(false)}
        onConfirm={handleDeleteConfirm}
        title=" Boss"
        message=" Boss "
        confirmText=""
        isDanger
      />
    </>
  );
};
