import React, { useState } from 'react';
import { Plus } from 'lucide-react';
import { useBoss } from '../context';
import { BossList } from '../components/bosses/BossList';
import { BossModal } from '../components/bosses/BossModal';
import { Boss } from '../types';

export const BossesPage: React.FC = () => {
  const { bosses } = useBoss();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingBoss, setEditingBoss] = useState<Boss | undefined>(undefined);

  const handleCreate = () => {
    setEditingBoss(undefined);
    setIsModalOpen(true);
  };

  const handleEdit = (boss: Boss) => {
    setEditingBoss(boss);
    setIsModalOpen(true);
  };

  const handleClose = () => {
    setIsModalOpen(false);
    setEditingBoss(undefined);
  };

  const activeBosses = bosses.filter((b) => b.status === 'active');
  const defeatedBosses = bosses.filter((b) => b.status === 'defeated');

  return (
    <div className="space-y-4">
      <div className="relative flex flex-col items-center justify-center pt-2 pb-2">
        <h1 className="text-2xl font-bold text-gray-900 mb-4">Boss </h1>
        <button
          onClick={handleCreate}
          className="flex items-center justify-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors shadow-sm w-full"
        >
          <Plus size={20} className="mr-2" />
          
        </button>
      </div>

      <div className="space-y-4">
        <BossList bosses={activeBosses} onEdit={handleEdit} />
      </div>

      {defeatedBosses.length > 0 && (
        <div className="space-y-4 pt-4 border-t border-gray-200">
          <h2 className="text-lg font-semibold text-gray-700"></h2>
          <BossList bosses={defeatedBosses} onEdit={handleEdit} />
        </div>
      )}

      <BossModal isOpen={isModalOpen} onClose={handleClose} initialData={editingBoss} />
    </div>
  );
};
