import React, { useState } from 'react';
import { Plus } from 'lucide-react';
import { useAttribute } from '../context';
import { AttributeList } from '../components/attributes/AttributeList';
import { AttributeModal } from '../components/attributes/AttributeModal';
import { ProfileCard } from '../components/profile/ProfileCard';
import { Attribute } from '../types';

export const AttributesPage: React.FC = () => {
  const { attributes } = useAttribute();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingAttribute, setEditingAttribute] = useState<Attribute | undefined>(undefined);

  const handleCreate = () => {
    setEditingAttribute(undefined);
    setIsModalOpen(true);
  };

  const handleEdit = (attribute: Attribute) => {
    setEditingAttribute(attribute);
    setIsModalOpen(true);
  };

  const handleClose = () => {
    setIsModalOpen(false);
    setEditingAttribute(undefined);
  };

  return (
    <div className="space-y-8">
      {/* Profile Card Section */}
      <ProfileCard />

      {/* Attributes List Section */}
      <div className="space-y-4">
        <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4 border-b pb-4">
          <h2 className="text-xl font-bold text-gray-900"></h2>
          
          <button
            onClick={handleCreate}
            className="flex items-center justify-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors shadow-sm"
          >
            <Plus size={20} className="mr-2" />
            
          </button>
        </div>

        <AttributeList attributes={attributes} onEdit={handleEdit} />
      </div>

      <AttributeModal isOpen={isModalOpen} onClose={handleClose} initialData={editingAttribute} />
    </div>
  );
};
