import React from 'react';
import { Boss } from '../../types';
import { BossItem } from './BossItem';

interface BossListProps {
  bosses: Boss[];
  onEdit: (boss: Boss) => void;
}

export const BossList: React.FC<BossListProps> = ({ bosses, onEdit }) => {
  if (bosses.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center py-12 text-center text-gray-400">
        <p className="text-lg font-medium mb-1"></p>
        <p className="text-sm"> Boss </p>
      </div>
    );
  }

  return (
    <div className="grid grid-cols-2 gap-4">
      {bosses.map((boss) => (
        <BossItem key={boss.id} boss={boss} onEdit={onEdit} />
      ))}
    </div>
  );
};
