import React, { useMemo, useState, useEffect, useRef } from 'react';
import { useUser } from '../../context';
import { useAttribute } from '../../context';
import { Briefcase, Coins, Users, Trophy, Crown, Sparkles, Star, User } from 'lucide-react';
import { ProfileModal } from './ProfileModal';
import clsx from 'clsx';
import { Dialog } from '@headlessui/react';
import { formatNumber } from '../../utils/numberUtils';

export const ProfileCard: React.FC = () => {
  const { profile } = useUser();
  const { attributes } = useAttribute();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [showLevelUp, setShowLevelUp] = useState(false);
  
  const totalAttributePoints = useMemo(() => {
    return attributes.reduce((sum, attr) => sum + attr.value, 0);
  }, [attributes]);

  const getRankInfo = (points: number) => {
    if (points >= 50000) return { 
      title: '', 
      rank: 5,
      bgClass: 'bg-gradient-to-r from-rose-100 via-purple-100 to-indigo-100 border-indigo-200',
      textClass: 'text-transparent bg-clip-text bg-gradient-to-r from-rose-600 via-purple-600 to-indigo-600 font-extrabold animate-pulse',
      icon: Crown,
      iconColor: 'text-purple-600',
      shadowClass: 'shadow-[0_0_20px_rgba(124,58,237,0.3)]',
      effect: 'animate-pulse'
    };
    if (points >= 20000) return { 
      title: '', 
      rank: 4,
      bgClass: 'bg-gradient-to-r from-amber-50 to-orange-100 border-orange-200',
      textClass: 'text-orange-600 font-bold drop-shadow-sm',
      icon: Star,
      iconColor: 'text-orange-500',
      shadowClass: 'shadow-[0_0_15px_rgba(249,115,22,0.3)]',
      effect: ''
    };
    if (points >= 10000) return { 
      title: '', 
      rank: 3,
      bgClass: 'bg-gradient-to-r from-purple-50 to-pink-50 border-purple-200',
      textClass: 'text-purple-600 font-bold',
      icon: Sparkles,
      iconColor: 'text-purple-500',
      shadowClass: 'shadow-md',
      effect: ''
    };
    if (points >= 5000) return { 
      title: '', 
      rank: 2,
      bgClass: 'bg-blue-50 border-blue-200',
      textClass: 'text-blue-600 font-semibold',
      icon: Trophy,
      iconColor: 'text-blue-500',
      shadowClass: 'shadow-sm',
      effect: ''
    };
    if (points >= 1000) return { 
      title: '', 
      rank: 1,
      bgClass: 'bg-green-50 border-green-200',
      textClass: 'text-green-600 font-medium',
      icon: Trophy,
      iconColor: 'text-green-500',
      shadowClass: 'shadow-sm',
      effect: ''
    };
    return { 
      title: '', 
      rank: 0,
      bgClass: 'bg-white border-gray-200',
      textClass: 'text-gray-500',
      icon: User,
      iconColor: 'text-gray-400',
      shadowClass: 'shadow-sm',
      effect: ''
    };
  };

  const currentRankInfo = getRankInfo(totalAttributePoints);
  const { title, rank, bgClass, textClass, icon: RankIcon, iconColor, shadowClass } = currentRankInfo;

  // Track previous rank to trigger level up notification
  const prevRankRef = useRef(rank);
  
  useEffect(() => {
    if (rank > prevRankRef.current) {
      setShowLevelUp(true);
      setTimeout(() => setShowLevelUp(false), 3000);
    }
    prevRankRef.current = rank;
  }, [rank]);

  return (
    <>
      {/* Level Up Notification Overlay */}
      {showLevelUp && (
        <div className="fixed inset-0 z-50 flex items-center justify-center pointer-events-none">
          <div className="bg-black/80 text-white px-8 py-6 rounded-2xl flex flex-col items-center animate-bounce-in backdrop-blur-md">
            <Sparkles className="text-yellow-400 w-12 h-12 mb-2 animate-spin-slow" />
            <div className="text-2xl font-bold text-yellow-400 mb-1"></div>
            <div className="text-lg"></div>
            <div className={clsx("text-xl font-bold mt-2", textClass)}>{title}</div>
          </div>
        </div>
      )}

      <div 
        className={clsx(
          "rounded-2xl border p-4 relative overflow-hidden transition-all duration-500 cursor-pointer hover:shadow-lg",
          bgClass,
          shadowClass
        )}
        onClick={() => setIsModalOpen(true)}
      >
        {/* Background Effects for High Ranks */}
        {rank >= 4 && (
          <div className="absolute -top-10 -right-10 w-32 h-32 bg-gradient-to-br from-white/40 to-transparent rounded-full blur-2xl pointer-events-none" />
        )}
        {rank >= 5 && (
          <div className="absolute -bottom-10 -left-10 w-32 h-32 bg-gradient-to-tr from-purple-200/30 to-transparent rounded-full blur-2xl pointer-events-none animate-pulse" />
        )}

        {/* Top Section: Avatar, Name, Title, Profession */}
        <div className="flex flex-row gap-3 md:gap-4 items-center mb-4">
           {/* Left: Avatar */}
           <div className="relative shrink-0">
             <div className={clsx(
               "w-20 h-20 md:w-24 md:h-24 rounded-full flex items-center justify-center text-3xl font-bold shadow-lg border-4 overflow-hidden",
               rank >= 4 ? "border-yellow-200 ring-2 ring-yellow-100" : "border-white",
               !profile.avatar && (profile.gender === 'female' ? "bg-pink-400 text-white" : "bg-blue-500 text-white")
             )}>
               {profile.avatar ? (
                 <img src={profile.avatar} alt={profile.name} className="w-full h-full object-cover" />
               ) : (
                 profile.name.charAt(0).toUpperCase()
               )}
             </div>
             {rank >= 3 && (
               <div className="absolute -bottom-1 -right-1 bg-white p-1.5 rounded-full shadow-md">
                 <RankIcon size={20} className={iconColor} />
               </div>
             )}
           </div>

           {/* Middle: Info */}
           <div className="flex-1 min-w-0 space-y-1">
              <div className="flex items-center gap-2">
                <h2 className="text-xl font-bold text-gray-900 truncate">
                  {profile.name}
                </h2>
                <span className={clsx("text-[10px] px-1.5 py-0.5 rounded-full border font-normal shrink-0", 
                  profile.gender === 'female' ? "text-pink-600 border-pink-200 bg-pink-50" : 
                  profile.gender === 'male' ? "text-blue-600 border-blue-200 bg-blue-50" :
                  "text-purple-600 border-purple-200 bg-purple-50"
                )}>
                  {profile.gender === 'male' ? '' : profile.gender === 'female' ? '' : '?'}
                </span>
              </div>
              
              <div className="flex items-center text-gray-600 text-sm">
                <Briefcase size={14} className="mr-1.5" />
                <span className="truncate">{profile.profession || ''}</span>
              </div>
           </div>

           {/* Right: Title */}
           <div className="flex flex-col items-center justify-center pl-2 border-l border-gray-200/50 shrink-0 w-24">
              <div className={clsx("text-2xl mb-1", rank >= 4 ? "animate-bounce" : "")}>
                {rank >= 5 ? '' : rank >= 4 ? '' : rank >= 3 ? '' : ''}
              </div>
              <div className={clsx("text-sm font-bold text-center break-words w-full", textClass)}>
                {title}
              </div>
              {rank >= 4 && <Sparkles size={12} className="text-yellow-500 animate-spin-slow mt-1" />}
           </div>
        </div>

        {/* Signature Section */}
        <div className="mb-3 px-1">
           <div className="text-xs text-gray-500 italic text-center bg-gray-50/50 rounded-lg py-1.5 px-2 border border-gray-100/50 line-clamp-2 break-words">
             "{profile.signature || ''}"
           </div>
        </div>

        {/* Bottom Section: Stats Grid */}
        <div className="grid grid-cols-3 gap-2 mt-2 pt-3 border-t border-gray-100/50">
           <div className="bg-white/60 backdrop-blur-sm p-2 rounded-lg border border-gray-100/50 shadow-sm flex flex-col items-center justify-center text-center">
             <div className="text-gray-500 mb-1"><Coins size={14} /></div>
             <div className="text-[10px] text-gray-400 mb-0.5"></div>
             <div className="font-bold text-sm text-gray-900 w-full truncate" title={`${profile.money.toLocaleString()}`}>{formatNumber(profile.money)}</div>
           </div>

           <div className="bg-white/60 backdrop-blur-sm p-2 rounded-lg border border-gray-100/50 shadow-sm flex flex-col items-center justify-center text-center">
             <div className="text-gray-500 mb-1"><Users size={14} /></div>
             <div className="text-[10px] text-gray-400 mb-0.5"></div>
             <div className="font-bold text-sm text-gray-900 w-full truncate" title={profile.fans.toLocaleString()}>{formatNumber(profile.fans)}</div>
           </div>

           <div className="bg-white/60 backdrop-blur-sm p-2 rounded-lg border border-gray-100/50 shadow-sm flex flex-col items-center justify-center text-center">
             <div className="text-gray-500 mb-1"><Trophy size={14} /></div>
             <div className="text-[10px] text-gray-400 mb-0.5"></div>
             <div className="font-bold text-sm text-gray-900 w-full truncate" title={totalAttributePoints.toLocaleString()}>{formatNumber(totalAttributePoints)}</div>
           </div>
         </div>
      </div>

      <ProfileModal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)} />
    </>
  );
};
