import React, { useState, useEffect } from 'react';
import { Dialog } from '@headlessui/react';
import { X, Sword, Shield, Heart, Zap, Brain, Smile, Star, Crown, Target, Book } from 'lucide-react';
import { Attribute } from '../../types';
import { useAttribute } from '../../context';
import { ImageUpload } from '../common/ImageUpload';
import clsx from 'clsx';

interface AttributeModalProps {
  isOpen: boolean;
  onClose: () => void;
  initialData?: Attribute;
}

const presetIcons = [
  { name: 'Sword', icon: Sword, label: '' },
  { name: 'Shield', icon: Shield, label: '' },
  { name: 'Heart', icon: Heart, label: '' },
  { name: 'Zap', icon: Zap, label: '' },
  { name: 'Brain', icon: Brain, label: '' },
  { name: 'Smile', icon: Smile, label: '' },
  { name: 'Star', icon: Star, label: '' },
  { name: 'Crown', icon: Crown, label: '' },
  { name: 'Target', icon: Target, label: '' },
  { name: 'Book', icon: Book, label: '' },
];

export const AttributeModal: React.FC<AttributeModalProps> = ({ isOpen, onClose, initialData }) => {
  const { addAttribute, updateAttribute } = useAttribute();
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [value, setValue] = useState(0);
  const [image, setImage] = useState<string | undefined>(undefined);

  useEffect(() => {
    if (initialData) {
      setName(initialData.name);
      setDescription(initialData.description || '');
      setValue(initialData.value);
      setImage(initialData.image);
    } else {
      setName('');
      setDescription('');
      setValue(0);
      setImage(undefined);
    }
  }, [initialData, isOpen]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!name.trim()) return;

    if (initialData) {
      updateAttribute({
        ...initialData,
        name,
        description: description || '',
        value,
        image,
      });
    } else {
      addAttribute({
        name,
        description: description || '',
        value,
        maxValue: 10000,
        image,
      });
    }
    onClose();
  };

  const handleIconSelect = (iconName: string) => {
    setImage(`icon:${iconName}`);
  };

  return (
    <Dialog open={isOpen} onClose={onClose} className="relative z-50">
      <div className="fixed inset-0 bg-black/30" aria-hidden="true" />

      <div className="fixed inset-0 flex items-center justify-center p-4">
        <Dialog.Panel className="w-full max-w-md rounded-2xl bg-white p-6 shadow-xl max-h-[90vh] overflow-y-auto">
          <div className="flex items-center justify-between mb-4">
            <Dialog.Title className="text-lg font-bold text-gray-900">
              {initialData ? '' : ''}
            </Dialog.Title>
            <button onClick={onClose} className="text-gray-400 hover:text-gray-500">
              <X size={20} />
            </button>
          </div>

          <form onSubmit={handleSubmit} className="space-y-4">
            {/* Icon Selection */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2"></label>
              <div className="grid grid-cols-5 gap-2 mb-4">
                {presetIcons.map((preset) => (
                  <button
                    key={preset.name}
                    type="button"
                    onClick={() => handleIconSelect(preset.name)}
                    className={clsx(
                      "flex flex-col items-center justify-center p-2 rounded-lg border transition-all",
                      image === `icon:${preset.name}` 
                        ? "border-blue-500 bg-blue-50 text-blue-600 ring-1 ring-blue-500" 
                        : "border-gray-200 hover:bg-gray-50 text-gray-600"
                    )}
                  >
                    <preset.icon size={20} className="mb-1" />
                    <span className="text-[10px]">{preset.label}</span>
                  </button>
                ))}
              </div>
            </div>

            {/* Image Upload (Alternative) */}
            <div className="flex justify-center mb-4 border-t border-gray-100 pt-4">
              <div className="w-full">
                <p className="text-xs text-center text-gray-500 mb-2"></p>
                <div className="flex justify-center">
                  <ImageUpload
                    currentImage={image?.startsWith('icon:') ? undefined : image}
                    onImageChange={setImage}
                    label=""
                    className="w-20 h-20"
                  />
                </div>
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1"></label>
              <input
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white text-gray-900"
                placeholder=" (, )..."
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1"></label>
              <input
                type="text"
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white text-gray-900"
                placeholder=" ()..."
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                {initialData ? ' ()' : ' (0-10000)'}
              </label>
              <input
                type="number"
                min="0"
                max="10000"
                value={value}
                onChange={(e) => setValue(parseInt(e.target.value) || 0)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-100 disabled:text-gray-500 bg-white text-gray-900"
                disabled={!!initialData}
              />
            </div>

            <div className="flex justify-end pt-4 space-x-3">
              <button
                type="button"
                onClick={onClose}
                className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200"
              >
                
              </button>
              <button
                type="submit"
                className="px-4 py-2 text-sm font-medium text-white bg-blue-600 rounded-lg hover:bg-blue-700"
              >
                
              </button>
            </div>
          </form>
        </Dialog.Panel>
      </div>
    </Dialog>
  );
};
