import React, { createContext, useContext, useEffect, useReducer } from 'react';
import { v4 as uuidv4 } from 'uuid';
import { Attribute } from '../types';
import { loadFromStorage, saveToStorage, STORAGE_KEYS } from '../utils/storage';
import { getCurrentISOString } from '../utils/dateUtils';

type AttributeAction =
  | { type: 'ADD_ATTRIBUTE'; payload: Omit<Attribute, 'id' | 'updatedAt'> }
  | { type: 'UPDATE_ATTRIBUTE'; payload: Attribute }
  | { type: 'DELETE_ATTRIBUTE'; payload: string }
  | { type: 'UPDATE_ATTRIBUTE_VALUE'; payload: { id: string; value: number } }
  | { type: 'SET_ATTRIBUTES'; payload: Attribute[] };

interface AttributeState {
  attributes: Attribute[];
}

interface AttributeContextType extends AttributeState {
  addAttribute: (attribute: Omit<Attribute, 'id' | 'updatedAt'>) => void;
  updateAttribute: (attribute: Attribute) => void;
  deleteAttribute: (id: string) => void;
  updateAttributeValue: (id: string, value: number) => void;
}

const AttributeContext = createContext<AttributeContextType | undefined>(undefined);

const attributeReducer = (state: AttributeState, action: AttributeAction): AttributeState => {
  switch (action.type) {
    case 'ADD_ATTRIBUTE':
      const newAttribute: Attribute = {
        ...action.payload,
        id: uuidv4(),
        updatedAt: getCurrentISOString(),
        value: Math.max(0, Math.min(10000, action.payload.value)), // Ensure initial value is within range
      };
      return { attributes: [newAttribute, ...state.attributes] };
    case 'UPDATE_ATTRIBUTE':
      return {
        attributes: state.attributes.map((attr) =>
          attr.id === action.payload.id ? { ...action.payload, updatedAt: getCurrentISOString() } : attr
        ),
      };
    case 'DELETE_ATTRIBUTE':
      return { attributes: state.attributes.filter((attr) => attr.id !== action.payload) };
    case 'UPDATE_ATTRIBUTE_VALUE':
      return {
        attributes: state.attributes.map((attr) => {
          if (attr.id === action.payload.id) {
            const newValue = Math.max(0, Math.min(10000, action.payload.value));
            return { ...attr, value: newValue, updatedAt: getCurrentISOString() };
          }
          return attr;
        }),
      };
    case 'SET_ATTRIBUTES':
      return { attributes: action.payload };
    default:
      return state;
  }
};

export const AttributeProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [state, dispatch] = useReducer(attributeReducer, {
    attributes: [],
  });

  useEffect(() => {
    const storedAttributes = loadFromStorage<Attribute[]>(STORAGE_KEYS.ATTRIBUTES, []);
    dispatch({ type: 'SET_ATTRIBUTES', payload: storedAttributes });
  }, []);

  useEffect(() => {
    saveToStorage(STORAGE_KEYS.ATTRIBUTES, state.attributes);
  }, [state.attributes]);

  const addAttribute = (attribute: Omit<Attribute, 'id' | 'updatedAt'>) => {
    dispatch({ type: 'ADD_ATTRIBUTE', payload: attribute });
  };

  const updateAttribute = (attribute: Attribute) => {
    dispatch({ type: 'UPDATE_ATTRIBUTE', payload: attribute });
  };

  const deleteAttribute = (id: string) => {
    dispatch({ type: 'DELETE_ATTRIBUTE', payload: id });
  };

  const updateAttributeValue = (id: string, value: number) => {
    dispatch({ type: 'UPDATE_ATTRIBUTE_VALUE', payload: { id, value } });
  };

  return (
    <AttributeContext.Provider value={{ ...state, addAttribute, updateAttribute, deleteAttribute, updateAttributeValue }}>
      {children}
    </AttributeContext.Provider>
  );
};

export const useAttribute = () => {
  const context = useContext(AttributeContext);
  if (context === undefined) {
    throw new Error('useAttribute must be used within a AttributeProvider');
  }
  return context;
};
