import React, { useState, useEffect } from 'react';
import { Dialog } from '@headlessui/react';
import { Book, X, RefreshCw, Settings, Plus, Trash2 } from 'lucide-react';
import { loadFromStorage, saveToStorage } from '../../utils/storage';

const DEFAULT_ANSWERS = [
  "", "", "", "", "", 
  "", "", "", "", 
  "", "", "", "",
  "", "", "", "",
  "", "", ""
];

const STORAGE_KEY_ANSWERS = 'life-game-answers-custom';

export const BookOfAnswers: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [currentAnswer, setCurrentAnswer] = useState<string | null>(null);
  const [isAnimating, setIsAnimating] = useState(false);
  const [customAnswers, setCustomAnswers] = useState<string[]>(() => 
    loadFromStorage(STORAGE_KEY_ANSWERS, DEFAULT_ANSWERS)
  );
  const [isSettingsOpen, setIsSettingsOpen] = useState(false);
  // Replaced single text state with array of strings
  const [answerInputs, setAnswerInputs] = useState<string[]>([]);

  const handleOpen = () => {
    setIsOpen(true);
    setCurrentAnswer(null); // Reset on open
  };

  const handleClose = () => {
    setIsOpen(false);
    setIsSettingsOpen(false);
  };

  const drawAnswer = () => {
    if (currentAnswer) return; // Only one draw per open
    
    setIsAnimating(true);
    setTimeout(() => {
      const randomIndex = Math.floor(Math.random() * customAnswers.length);
      setCurrentAnswer(customAnswers[randomIndex]);
      setIsAnimating(false);
    }, 1500);
  };

  const handleSaveCustom = () => {
    // Filter out empty answers
    const validAnswers = answerInputs.filter(ans => ans.trim() !== '');
    if (validAnswers.length > 0) {
      setCustomAnswers(validAnswers);
      saveToStorage(STORAGE_KEY_ANSWERS, validAnswers);
      setIsSettingsOpen(false);
    } else {
        alert("");
    }
  };

  useEffect(() => {
    if (isSettingsOpen) {
      // Initialize inputs from current custom answers
      setAnswerInputs([...customAnswers]);
    }
  }, [isSettingsOpen, customAnswers]);

  const updateAnswerInput = (index: number, value: string) => {
    const newInputs = [...answerInputs];
    newInputs[index] = value;
    setAnswerInputs(newInputs);
  };

  const addAnswerInput = () => {
    setAnswerInputs([...answerInputs, '']);
  };

  const removeAnswerInput = (index: number) => {
    const newInputs = answerInputs.filter((_, i) => i !== index);
    setAnswerInputs(newInputs);
  };

  return (
    <>
      {/* Trigger Button */}
      <button
        onClick={handleOpen}
        className="fixed bottom-20 left-4 z-40 p-3 bg-purple-600 text-white rounded-full shadow-lg hover:bg-purple-700 transition-all hover:scale-110 active:scale-95"
        title=""
      >
        <Book size={24} />
      </button>

      {/* Main Modal */}
      <Dialog open={isOpen} onClose={handleClose} className="relative z-50">
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm" aria-hidden="true" />
        
        <div className="fixed inset-0 flex items-center justify-center p-4">
          <Dialog.Panel className="w-full max-w-sm aspect-[3/4] bg-[#2a1b3d] border-4 border-[#d4af37] rounded-lg shadow-2xl relative overflow-hidden flex flex-col items-center justify-center text-center p-8 max-h-[90vh]">
            
            {/* Corner Decorations */}
            <div className="absolute top-2 left-2 w-4 h-4 border-t-2 border-l-2 border-[#d4af37]" />
            <div className="absolute top-2 right-2 w-4 h-4 border-t-2 border-r-2 border-[#d4af37]" />
            <div className="absolute bottom-2 left-2 w-4 h-4 border-b-2 border-l-2 border-[#d4af37]" />
            <div className="absolute bottom-2 right-2 w-4 h-4 border-b-2 border-r-2 border-[#d4af37]" />

            <button 
              onClick={handleClose}
              className="absolute top-4 right-4 text-[#d4af37] hover:text-white transition-colors"
            >
              <X size={24} />
            </button>

            <button
              onClick={() => setIsSettingsOpen(true)}
              className="absolute top-4 left-4 text-[#d4af37] hover:text-white transition-colors"
            >
              <Settings size={20} />
            </button>

            {!currentAnswer && !isAnimating && (
              <div className="space-y-6 animate-fade-in">
                <h2 className="text-2xl font-serif text-[#d4af37] tracking-wider"></h2>
                <p className="text-gray-300 font-light italic">
                  <br/>
                </p>
                <button
                  onClick={drawAnswer}
                  className="px-8 py-3 bg-[#d4af37] text-[#2a1b3d] font-bold rounded-full hover:bg-[#f3cf55] transition-colors shadow-[0_0_15px_rgba(212,175,55,0.5)]"
                >
                  
                </button>
              </div>
            )}

            {isAnimating && (
              <div className="flex flex-col items-center justify-center">
                <RefreshCw size={48} className="text-[#d4af37] animate-spin mb-4" />
                <p className="text-gray-300 animate-pulse">...</p>
              </div>
            )}

            {currentAnswer && !isAnimating && (
              <div className="animate-zoom-in">
                <div className="mb-6">
                  <span className="text-6xl text-[#d4af37] opacity-20"></span>
                </div>
                <h3 className="text-3xl font-bold text-white mb-6 leading-relaxed font-serif">
                  {currentAnswer}
                </h3>
                <div className="mt-6">
                  <span className="text-6xl text-[#d4af37] opacity-20"></span>
                </div>
              </div>
            )}
          </Dialog.Panel>
        </div>
      </Dialog>

      {/* Settings Modal */}
      <Dialog open={isSettingsOpen} onClose={() => setIsSettingsOpen(false)} className="relative z-[60]">
        <div className="fixed inset-0 bg-black/70" aria-hidden="true" />
        <div className="fixed inset-0 flex items-center justify-center p-4">
          <Dialog.Panel className="w-full max-w-md bg-white rounded-xl p-6 shadow-xl max-h-[85vh] flex flex-col">
            <div className="flex justify-between items-center mb-4 shrink-0">
                <div>
                    <h3 className="text-lg font-bold text-gray-900"></h3>
                    <p className="text-xs text-gray-500"></p>
                </div>
                <button 
                    onClick={addAnswerInput}
                    className="p-2 bg-purple-100 text-purple-600 rounded-full hover:bg-purple-200 transition-colors"
                    title=""
                >
                    <Plus size={20} />
                </button>
            </div>
            
            <div className="flex-1 overflow-y-auto space-y-2 pr-1 mb-4">
                {answerInputs.map((answer, index) => (
                    <div key={index} className="flex items-center space-x-2 animate-fade-in-up">
                        <span className="text-xs text-gray-400 w-6 text-right">{index + 1}.</span>
                        <input 
                            type="text"
                            value={answer}
                            onChange={(e) => updateAnswerInput(index, e.target.value)}
                            className="flex-1 p-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-purple-500 focus:outline-none text-sm bg-white text-gray-900"
                            placeholder="..."
                        />
                        <button
                            onClick={() => removeAnswerInput(index)}
                            className="p-2 text-gray-400 hover:text-red-500 hover:bg-red-50 rounded-lg transition-colors"
                            title=""
                        >
                            <Trash2 size={16} />
                        </button>
                    </div>
                ))}
                {answerInputs.length === 0 && (
                    <div className="text-center text-gray-400 py-8">
                         + 
                    </div>
                )}
            </div>

            <div className="flex justify-end pt-2 border-t border-gray-100 shrink-0 space-x-3">
              <button
                onClick={() => setIsSettingsOpen(false)}
                className="px-4 py-2 text-gray-600 hover:bg-gray-100 rounded-lg text-sm font-medium"
              >
                
              </button>
              <button
                onClick={handleSaveCustom}
                className="px-4 py-2 bg-purple-600 text-white rounded-lg hover:bg-purple-700 text-sm font-medium"
              >
                 ({answerInputs.filter(a => a.trim()).length})
              </button>
            </div>
          </Dialog.Panel>
        </div>
      </Dialog>
    </>
  );
};
