import React, { useState } from 'react';
import { Upload, X } from 'lucide-react';
import { convertFileToBase64 } from '../../utils/fileUtils';
import { ImageCropperModal } from './ImageCropperModal';

interface ImageUploadProps {
  label: string;
  currentImage?: string;
  onImageChange: (base64: string) => void;
  aspectRatio?: number;
  presetImages?: string[];
  className?: string;
}

export const ImageUpload: React.FC<ImageUploadProps> = ({ label, currentImage, onImageChange, aspectRatio = 1, presetImages, className }) => {
  const [isCropperOpen, setIsCropperOpen] = useState(false);
  const [selectedImage, setSelectedImage] = useState<string>('');

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    if (file.size > 2 * 1024 * 1024) {
      alert(' 2MB');
      return;
    }

    try {
      const base64 = await convertFileToBase64(file);
      setSelectedImage(base64);
      setIsCropperOpen(true);
    } catch (error) {
      console.error('Error converting file:', error);
      alert('');
    }
    
    // Reset input
    e.target.value = '';
  };

  const handleRemove = () => {
    onImageChange('');
  };

  const handleCropComplete = (croppedImage: string) => {
    onImageChange(croppedImage);
    setIsCropperOpen(false);
  };

  return (
    <div>
      <div className="flex items-center space-x-4">
        <div className={`relative rounded-lg bg-gray-100 border-2 border-dashed border-gray-300 flex items-center justify-center overflow-hidden group ${className || 'w-20 h-20'}`}>
          {currentImage ? (
            <>
              <img src={currentImage} alt="Preview" className="w-full h-full object-cover" />
              <button
                type="button"
                onClick={handleRemove}
                className="absolute inset-0 bg-black/50 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity text-white"
              >
                <X size={20} />
              </button>
            </>
          ) : (
            <Upload size={24} className="text-gray-400" />
          )}
        </div>
        
        <div className="flex-1">
          <input
            type="file"
            accept="image/*"
            onChange={handleFileChange}
            className="block w-full text-sm text-gray-500
              file:mr-4 file:py-2 file:px-4
              file:rounded-full file:border-0
              file:text-sm file:font-semibold
              file:bg-blue-50 file:text-blue-700
              hover:file:bg-blue-100
              cursor-pointer"
          />
          <p className="mt-1 text-xs text-gray-500"> JPG, PNG, GIF ( 2MB)</p>
        </div>
      </div>

      {presetImages && presetImages.length > 0 && (
        <div className="mt-4">
          <label className="block text-sm font-medium text-gray-700 mb-2"></label>
          <div className="flex flex-wrap gap-2">
            {presetImages.map((image, index) => (
              <button
                key={index}
                type="button"
                onClick={() => onImageChange(image)}
                className="w-16 h-16 border rounded-lg overflow-hidden hover:ring-2 hover:ring-blue-500 transition-all"
              >
                <img src={image} alt={`Preset ${index}`} className="w-full h-full object-cover" />
              </button>
            ))}
          </div>
        </div>
      )}

      <ImageCropperModal
        isOpen={isCropperOpen}
        onClose={() => setIsCropperOpen(false)}
        imageSrc={selectedImage}
        onCropComplete={handleCropComplete}
        aspectRatio={aspectRatio}
      />
    </div>
  );
};
