import React, { useState } from 'react';
import { Dialog } from '@headlessui/react';
import { X, Sword, Gift, Trash2, Edit2 } from 'lucide-react';
import { Boss } from '../../types';
import { useBoss, useAttribute, useUser } from '../../context';
import clsx from 'clsx';
import { ConfirmModal } from '../common/ConfirmModal';

interface BossAttackModalProps {
  isOpen: boolean;
  onClose: () => void;
  boss: Boss;
  onEdit: () => void;
  onDelete: () => void;
}

export const BossAttackModal: React.FC<BossAttackModalProps> = ({ 
  isOpen, 
  onClose, 
  boss,
  onEdit,
  onDelete
}) => {
  const { updateBossProgress } = useBoss();
  const { updateAttributeValue, attributes } = useAttribute();
  const { profile, updateProfile } = useUser();
  const [damageInput, setDamageInput] = useState('');
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);

  const handleAttack = () => {
    const damage = parseInt(damageInput);
    if (!isNaN(damage) && damage > 0) {
      const newHealth = Math.max(0, boss.currentHealth - damage);
      updateBossProgress(boss.id, newHealth);
      
      // Check if boss is just defeated
      if (newHealth === 0 && boss.currentHealth > 0) {
        // Apply rewards
        if (boss.rewards && boss.rewards.length > 0) {
          boss.rewards.forEach(reward => {
            if (reward.type === 'attribute' && reward.attributeId) {
              const attribute = attributes.find(a => a.id === reward.attributeId);
              if (attribute) {
                updateAttributeValue(attribute.id, attribute.value + reward.value);
              }
            } else if (reward.type === 'money') {
              updateProfile({ money: profile.money + reward.value });
            }
          });
        }
      }
      
      setDamageInput('');
    }
  };

  const isDefeated = boss.status === 'defeated';
  const percentage = Math.round((boss.currentHealth / boss.maxHealth) * 100);

  return (
    <Dialog open={isOpen} onClose={onClose} className="relative z-50">
      <div className="fixed inset-0 bg-black/30 backdrop-blur-sm" aria-hidden="true" />

      <div className="fixed inset-0 flex items-center justify-center p-4">
        <Dialog.Panel className="w-full max-w-sm rounded-2xl bg-white p-6 shadow-xl border border-gray-100 relative overflow-hidden">
            {/* Background decoration */}
            <div className={clsx(
                "absolute top-0 left-0 right-0 h-24 bg-gradient-to-b opacity-20 pointer-events-none",
                isDefeated ? "from-yellow-200 to-transparent" : "from-red-200 to-transparent"
            )} />

            <button 
                onClick={onClose}
                className="absolute top-4 right-4 z-10 p-1 text-gray-400 hover:text-gray-600 bg-white/50 rounded-full backdrop-blur-sm"
            >
                <X size={20} />
            </button>

            <div className="flex flex-col items-center relative z-10">
                 {/* Large Avatar */}
                <div className={clsx(
                    "w-24 h-24 rounded-2xl flex items-center justify-center font-bold text-white shadow-lg mb-4 overflow-hidden border-4 border-white",
                    isDefeated ? "bg-yellow-200" : "bg-red-500"
                )}>
                    {boss.avatar ? (
                        <img src={boss.avatar} alt={boss.name} className="w-full h-full object-cover" />
                    ) : (
                        <span className="text-4xl">{boss.name.charAt(0).toUpperCase()}</span>
                    )}
                </div>

                <Dialog.Title className="text-xl font-bold text-gray-900 text-center mb-1">
                    {boss.name}
                </Dialog.Title>
                
                <p className="text-sm text-gray-500 text-center mb-6 px-4">
                    {boss.description}
                </p>

                {/* Stats Summary */}
                <div className="w-full bg-gray-50 rounded-xl p-4 mb-6">
                    <div className="flex justify-between items-end mb-2">
                        <span className="text-sm text-gray-500 font-medium"></span>
                        <div className="text-right">
                             <span className="text-xl font-bold text-gray-800">{boss.currentHealth}</span>
                             <span className="text-sm text-gray-400 ml-1">/ {boss.maxHealth}</span>
                        </div>
                    </div>
                    <div className="h-3 bg-gray-200 rounded-full overflow-hidden border border-gray-100">
                        <div
                            className={clsx(
                                'h-full transition-all duration-500 ease-out',
                                isDefeated ? 'bg-gray-400' : percentage < 30 ? 'bg-red-500' : 'bg-blue-500'
                            )}
                            style={{ width: `${percentage}%` }}
                        />
                    </div>
                </div>

                {/* Attack Controls */}
                {!isDefeated ? (
                    <div className="w-full space-y-3">
                        <div className="relative">
                            <input
                                type="number"
                                value={damageInput}
                                onChange={(e) => setDamageInput(e.target.value)}
                                placeholder="..."
                                className="w-full px-4 py-3 pl-10 text-base border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-red-500 bg-white text-gray-900 transition-colors"
                                autoFocus
                            />
                            <Sword className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" size={18} />
                        </div>
                        <button
                            onClick={handleAttack}
                            disabled={!damageInput}
                            className="w-full py-3 bg-red-500 text-white text-base font-bold rounded-xl hover:bg-red-600 disabled:opacity-50 disabled:cursor-not-allowed transition-all active:scale-[0.98] shadow-lg shadow-red-200"
                        >
                            
                        </button>
                    </div>
                ) : (
                    <div className="w-full p-4 bg-yellow-50 text-yellow-800 text-center rounded-xl font-bold border border-yellow-100 flex flex-col items-center">
                        <Gift size={32} className="mb-2 text-yellow-500" />
                        <span>Boss </span>
                    </div>
                )}

                {/* Rewards Preview */}
                {boss.rewards && boss.rewards.length > 0 && (
                    <div className="mt-6 w-full">
                        <p className="text-xs text-gray-400 text-center mb-2 font-medium uppercase tracking-wider"></p>
                        <div className="flex flex-wrap justify-center gap-2">
                        {boss.rewards.map((reward, idx) => {
                            if (reward.type === 'attribute' && reward.attributeId) {
                                const attrName = attributes.find(a => a.id === reward.attributeId)?.name;
                                if (!attrName) return null;
                                return (
                                    <span key={idx} className="flex items-center text-xs bg-white text-gray-700 px-3 py-1.5 rounded-full border border-gray-100 shadow-sm">
                                        <Gift size={12} className="mr-1.5 text-yellow-500" />
                                        {attrName} <span className="text-green-600 font-bold ml-1">+{reward.value}</span>
                                    </span>
                                );
                            } else if (reward.type === 'money') {
                                return (
                                    <span key={idx} className="flex items-center text-xs bg-white text-gray-700 px-3 py-1.5 rounded-full border border-gray-100 shadow-sm">
                                        <Gift size={12} className="mr-1.5 text-green-500" />
                                         <span className="text-green-600 font-bold ml-1">+{reward.value}</span>
                                    </span>
                                );
                            }
                            return null;
                        })}
                        </div>
                    </div>
                )}
            </div>

            {/* Footer Actions */}
            <div className="absolute bottom-4 left-4 right-4 flex justify-between items-center opacity-0 hover:opacity-100 transition-opacity">
               <button
                  onClick={() => {
                      onEdit();
                      onClose();
                  }}
                  className="p-2 text-gray-300 hover:text-blue-500 transition-colors"
                  title=""
                >
                  <Edit2 size={16} />
                </button>
                <button
                  onClick={() => setIsDeleteModalOpen(true)}
                  className="p-2 text-gray-300 hover:text-red-500 transition-colors"
                  title=""
                >
                  <Trash2 size={16} />
                </button>
            </div>
        </Dialog.Panel>
      </div>

      <ConfirmModal
        isOpen={isDeleteModalOpen}
        onClose={() => setIsDeleteModalOpen(false)}
        onConfirm={() => {
          onDelete();
          onClose();
        }}
        title=""
        message=" Boss "
        confirmText=""
        isDanger
      />
    </Dialog>
  );
};
