import React, { useState } from 'react';
import { Dialog } from '@headlessui/react';
import { X, Trash2 } from 'lucide-react';
import { useUser } from '../../context';
import { ImageUpload } from '../common/ImageUpload';
import { ConfirmModal } from '../common/ConfirmModal';

interface SettingsModalProps {
  isOpen: boolean;
  onClose: () => void;
}

export const SettingsModal: React.FC<SettingsModalProps> = ({ isOpen, onClose }) => {
  const { profile, updateProfile } = useUser();
  const [bgImage, setBgImage] = useState<string | undefined>(profile.backgroundImage);
  const [isResetModalOpen, setIsResetModalOpen] = useState(false);

  const handleSaveBg = () => {
    updateProfile({ backgroundImage: bgImage });
    onClose();
  };

  const handleResetData = () => {
    localStorage.clear();
    window.location.reload();
  };

  return (
    <>
      <Dialog open={isOpen} onClose={onClose} className="relative z-50">
        <div className="fixed inset-0 bg-black/30" aria-hidden="true" />

        <div className="fixed inset-0 flex items-center justify-center p-4">
          <Dialog.Panel className="w-full max-w-md rounded-2xl bg-white p-6 shadow-xl max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between mb-6">
              <Dialog.Title className="text-lg font-bold text-gray-900">
                
              </Dialog.Title>
              <button onClick={onClose} className="text-gray-400 hover:text-gray-500">
                <X size={20} />
              </button>
            </div>

            <div className="space-y-6">
              {/* Background Image Setting */}
              <div>
                <h3 className="text-sm font-medium text-gray-700 mb-2"></h3>
                <ImageUpload
                  label=""
                  currentImage={bgImage}
                  onImageChange={(img) => setBgImage(img || undefined)}
                  aspectRatio={window.innerWidth / window.innerHeight}
                />
                <div className="mt-2 flex justify-end">
                  <button
                    onClick={handleSaveBg}
                    className="px-4 py-2 text-sm font-medium text-white bg-blue-600 rounded-lg hover:bg-blue-700"
                  >
                    
                  </button>
                </div>
              </div>

              <hr className="border-gray-200" />

              {/* Danger Zone */}
              <div>
                <h3 className="text-sm font-medium text-red-600 mb-2"></h3>
                <button
                  onClick={() => setIsResetModalOpen(true)}
                  className="w-full flex items-center justify-center px-4 py-3 border border-red-200 text-red-600 bg-red-50 rounded-lg hover:bg-red-100 transition-colors"
                >
                  <Trash2 size={18} className="mr-2" />
                  
                </button>
              </div>
            </div>
          </Dialog.Panel>
        </div>
      </Dialog>
      
      <ConfirmModal
        isOpen={isResetModalOpen}
        onClose={() => setIsResetModalOpen(false)}
        onConfirm={handleResetData}
        title=""
        message=""
        confirmText=""
        isDanger
      />
    </>
  );
};
