export const STORAGE_KEYS = {
  TASKS: 'life-game-tasks',
  BOSSES: 'life-game-bosses',
  ATTRIBUTES: 'life-game-attributes',
  USER_PROFILE: 'life-game-user-profile',
};

export const loadFromStorage = <T>(key: string, defaultValue: T): T => {
  try {
    const item = localStorage.getItem(key);
    return item ? JSON.parse(item) : defaultValue;
  } catch (error) {
    console.error(`Error loading from storage key "${key}":`, error);
    return defaultValue;
  }
};

export const saveToStorage = <T>(key: string, value: T): void => {
  try {
    localStorage.setItem(key, JSON.stringify(value));
  } catch (error) {
    console.error(`Error saving to storage key "${key}":`, error);
  }
};
