import React, { useState, useEffect } from 'react';
import { Trash2, Zap, Sword, Shield, Heart, Brain, Smile, Star, Crown, Target, Book } from 'lucide-react';
import { Attribute } from '../../types';
import { useAttribute } from '../../context';
import clsx from 'clsx';
import { ConfirmModal } from '../common/ConfirmModal';

interface AttributeItemProps {
  attribute: Attribute;
  onEdit: (attribute: Attribute) => void;
}

const iconMap: Record<string, React.ElementType> = {
  Sword, Shield, Heart, Zap, Brain, Smile, Star, Crown, Target, Book
};

export const AttributeItem: React.FC<AttributeItemProps> = ({ attribute, onEdit }) => {
  const { deleteAttribute } = useAttribute();
  const [localValue, setLocalValue] = useState(attribute.value);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);

  useEffect(() => {
    setLocalValue(attribute.value);
  }, [attribute.value]);

  const handleDeleteClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    setIsDeleteModalOpen(true);
  };

  const handleDeleteConfirm = () => {
    deleteAttribute(attribute.id);
  };

  const percentage = Math.round((localValue / 10000) * 100);

  // Dynamic color based on value
  const getColorClass = (val: number) => {
    if (val < 3000) return 'text-red-500 bg-red-500';
    if (val < 7000) return 'text-yellow-500 bg-yellow-500';
    return 'text-green-500 bg-green-500';
  };
  
  const colorClass = getColorClass(localValue);
  const bgClass = colorClass.split(' ')[1];

  const renderIcon = () => {
    if (attribute.image?.startsWith('icon:')) {
      const iconName = attribute.image.split(':')[1];
      const IconComponent = iconMap[iconName] || Zap;
      return (
        <div className={clsx('p-4 rounded-2xl bg-opacity-10 shrink-0', bgClass.replace('bg-', 'bg-opacity-10 text-'))}>
          <IconComponent size={48} className={clsx(colorClass.split(' ')[0])} />
        </div>
      );
    }
    
    if (attribute.image) {
      return (
        <div className="w-24 h-24 rounded-2xl overflow-hidden border border-gray-100 shrink-0">
          <img src={attribute.image} alt={attribute.name} className="w-full h-full object-cover" />
        </div>
      );
    }

    return (
      <div className={clsx('p-4 rounded-2xl bg-opacity-10 shrink-0', bgClass.replace('bg-', 'bg-opacity-10 text-'))}>
        <Zap size={48} className={clsx(colorClass.split(' ')[0])} />
      </div>
    );
  };

  return (
    <>
      <div 
        className="bg-white rounded-lg shadow-sm border border-gray-100 p-3 transition-all hover:shadow-md cursor-pointer"
        onClick={() => onEdit(attribute)}
      >
        {/* Header: Smaller */}
        <div className="flex justify-between items-start mb-2">
          <div className="flex items-center space-x-4">
            {renderIcon()}
            
            <div className="min-w-0">
              <h3 className="text-xl font-bold text-gray-900 truncate">{attribute.name}</h3>
              <span className="text-sm text-gray-500 block truncate max-w-[150px]">
                {attribute.description}
              </span>
            </div>
          </div>
          <div className="flex space-x-1 shrink-0">
             {/* Edit Button Removed */}
            <button
              onClick={handleDeleteClick}
              className="p-1.5 text-gray-400 hover:text-red-500 hover:bg-red-50 rounded-lg transition-colors"
            >
              <Trash2 size={14} />
            </button>
          </div>
        </div>

        {/* Value & Progress: Larger */}
        <div className="space-y-2">
          <div className="w-full h-6 bg-gray-100 rounded-full overflow-hidden relative">
            <div 
              className={clsx("h-full transition-all duration-500 flex items-center justify-end px-2", colorClass.split(' ')[1])} 
              style={{ width: `${Math.max(percentage, 0)}%` }}
            >
               {percentage >= 10 && (
                 <span className="text-xs text-white/90 font-medium whitespace-nowrap">
                   {localValue} / 10000
                 </span>
               )}
            </div>
            {percentage < 10 && (
              <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                <span className="text-xs text-gray-500 font-medium">
                  {localValue} / 10000
                </span>
              </div>
            )}
          </div>
        </div>
      </div>

      <ConfirmModal
        isOpen={isDeleteModalOpen}
        onClose={() => setIsDeleteModalOpen(false)}
        onConfirm={handleDeleteConfirm}
        title=""
        message=""
        confirmText=""
        isDanger
      />
    </>
  );
};
