import React from 'react';
import { NavLink } from 'react-router-dom';
import { CheckSquare, Sword, User } from 'lucide-react';
import clsx from 'clsx';

const navItems = [
  { path: '/tasks', label: '', icon: CheckSquare },
  { path: '/bosses', label: 'Boss', icon: Sword },
  { path: '/attributes', label: '', icon: User },
];

export const BottomNavigation: React.FC = () => {
  return (
    <nav className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 flex justify-around items-center h-16 md:hidden z-50">
      {navItems.map((item) => (
        <NavLink
          key={item.path}
          to={item.path}
          className={({ isActive }) =>
            clsx(
              'flex flex-col items-center justify-center w-full h-full space-y-1',
              isActive ? 'text-blue-700' : 'text-gray-500 hover:text-blue-500'
            )
          }
        >
          <item.icon size={24} />
          <span className="text-xs font-medium">{item.label}</span>
        </NavLink>
      ))}
    </nav>
  );
};
