import React, { useState, useEffect } from 'react';
import { Dialog } from '@headlessui/react';
import { X, Sword } from 'lucide-react';
import { Boss, Reward } from '../../types';
import { useBoss } from '../../context';
import { ImageUpload } from '../common/ImageUpload';
import { RewardSelector } from '../common/RewardSelector';

import { BOSS_PRESETS } from '../../utils/emojiUtils';

interface BossModalProps {
  isOpen: boolean;
  onClose: () => void;
  initialData?: Boss;
}

export const BossModal: React.FC<BossModalProps> = ({ isOpen, onClose, initialData }) => {
  const { addBoss, updateBoss } = useBoss();
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [difficulty, setDifficulty] = useState<Boss['difficulty']>(1);
  const [maxHealth, setMaxHealth] = useState(100);
  const [avatar, setAvatar] = useState<string | undefined>(undefined);
  const [rewards, setRewards] = useState<Reward[]>([]);

  useEffect(() => {
    if (initialData) {
      setName(initialData.name);
      setDescription(initialData.description);
      setDifficulty(initialData.difficulty);
      setMaxHealth(initialData.maxHealth);
      setAvatar(initialData.avatar);
      setRewards(initialData.rewards || []);
    } else {
      setName('');
      setDescription('');
      setDifficulty(1);
      setMaxHealth(100);
      setAvatar(undefined);
      setRewards([]);
    }
  }, [initialData, isOpen]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!name.trim()) return;

    if (initialData) {
      updateBoss({
        ...initialData,
        name,
        description,
        difficulty,
        maxHealth,
        avatar,
        rewards,
        // If maxHealth changes, we might want to adjust currentHealth proportionally or keep it?
        // For simplicity, let's keep currentHealth but cap it at new maxHealth.
        currentHealth: Math.min(initialData.currentHealth, maxHealth),
      });
    } else {
      addBoss({
        name,
        description,
        difficulty,
        maxHealth,
        currentHealth: maxHealth,
        status: 'active',
        avatar,
        rewards,
      });
    }
    onClose();
  };

  return (
    <Dialog open={isOpen} onClose={onClose} className="relative z-50">
      <div className="fixed inset-0 bg-black/30" aria-hidden="true" />

      <div className="fixed inset-0 flex items-center justify-center p-4">
        <Dialog.Panel className="w-full max-w-md rounded-2xl bg-white p-6 shadow-xl max-h-[90vh] overflow-y-auto">
          <div className="flex items-center justify-between mb-4">
            <Dialog.Title className="text-lg font-bold text-gray-900">
              {initialData ? ' Boss' : ''}
            </Dialog.Title>
            <button onClick={onClose} className="text-gray-400 hover:text-gray-500">
              <X size={20} />
            </button>
          </div>

          <form onSubmit={handleSubmit} className="space-y-4">
            <ImageUpload
              label="Boss "
              currentImage={avatar}
              onImageChange={(img) => setAvatar(img || undefined)}
              presetImages={BOSS_PRESETS}
            />

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1"></label>
              <input
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white text-gray-900"
                placeholder="Boss ..."
                required
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1"></label>
              <textarea
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 min-h-[80px] bg-white text-gray-900"
                placeholder="..."
              />
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> (1-5)</label>
                <div className="flex items-center space-x-1 mt-2">
                  {[1, 2, 3, 4, 5].map((level) => (
                    <button
                      key={level}
                      type="button"
                      onClick={() => setDifficulty(level as Boss['difficulty'])}
                      className="focus:outline-none"
                    >
                      <Sword
                        size={20}
                        className={level <= difficulty ? 'text-red-500' : 'text-gray-300'}
                        fill={level <= difficulty ? 'currentColor' : 'none'}
                      />
                    </button>
                  ))}
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> (HP)</label>
                <input
                  type="number"
                  min="1"
                  value={maxHealth}
                  onChange={(e) => setMaxHealth(parseInt(e.target.value) || 100)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white text-gray-900"
                />
              </div>
            </div>

            <RewardSelector rewards={rewards} onChange={setRewards} />

            <div className="flex justify-end pt-4 space-x-3">
              <button
                type="button"
                onClick={onClose}
                className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200"
              >
                
              </button>
              <button
                type="submit"
                className="px-4 py-2 text-sm font-medium text-white bg-blue-600 rounded-lg hover:bg-blue-700"
              >
                
              </button>
            </div>
          </form>
        </Dialog.Panel>
      </div>
    </Dialog>
  );
};
