import React, { createContext, useContext, useEffect, useReducer } from 'react';
import { v4 as uuidv4 } from 'uuid';
import { Boss } from '../types';
import { loadFromStorage, saveToStorage, STORAGE_KEYS } from '../utils/storage';
import { getCurrentISOString } from '../utils/dateUtils';

type BossAction =
  | { type: 'ADD_BOSS'; payload: Omit<Boss, 'id' | 'createdAt'> }
  | { type: 'UPDATE_BOSS'; payload: Boss }
  | { type: 'DELETE_BOSS'; payload: string }
  | { type: 'UPDATE_BOSS_PROGRESS'; payload: { id: string; currentHealth: number } }
  | { type: 'SET_BOSSES'; payload: Boss[] };

interface BossState {
  bosses: Boss[];
}

interface BossContextType extends BossState {
  addBoss: (boss: Omit<Boss, 'id' | 'createdAt'>) => void;
  updateBoss: (boss: Boss) => void;
  deleteBoss: (id: string) => void;
  updateBossProgress: (id: string, currentHealth: number) => void;
}

const BossContext = createContext<BossContextType | undefined>(undefined);

const bossReducer = (state: BossState, action: BossAction): BossState => {
  switch (action.type) {
    case 'ADD_BOSS':
      const newBoss: Boss = {
        ...action.payload,
        id: uuidv4(),
        createdAt: getCurrentISOString(),
        status: 'active',
      };
      return { bosses: [newBoss, ...state.bosses] };
    case 'UPDATE_BOSS':
      return {
        bosses: state.bosses.map((boss) =>
          boss.id === action.payload.id ? action.payload : boss
        ),
      };
    case 'DELETE_BOSS':
      return { bosses: state.bosses.filter((boss) => boss.id !== action.payload) };
    case 'UPDATE_BOSS_PROGRESS':
      return {
        bosses: state.bosses.map((boss) => {
          if (boss.id === action.payload.id) {
            const newHealth = Math.max(0, Math.min(boss.maxHealth, action.payload.currentHealth));
            const newStatus = newHealth === 0 ? 'defeated' : 'active';
            return { ...boss, currentHealth: newHealth, status: newStatus };
          }
          return boss;
        }),
      };
    case 'SET_BOSSES':
      return { bosses: action.payload };
    default:
      return state;
  }
};

export const BossProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [state, dispatch] = useReducer(bossReducer, {
    bosses: [],
  });

  useEffect(() => {
    const storedBosses = loadFromStorage<Boss[]>(STORAGE_KEYS.BOSSES, []);
    dispatch({ type: 'SET_BOSSES', payload: storedBosses });
  }, []);

  useEffect(() => {
    saveToStorage(STORAGE_KEYS.BOSSES, state.bosses);
  }, [state.bosses]);

  const addBoss = (boss: Omit<Boss, 'id' | 'createdAt'>) => {
    dispatch({ type: 'ADD_BOSS', payload: boss });
  };

  const updateBoss = (boss: Boss) => {
    dispatch({ type: 'UPDATE_BOSS', payload: boss });
  };

  const deleteBoss = (id: string) => {
    dispatch({ type: 'DELETE_BOSS', payload: id });
  };

  const updateBossProgress = (id: string, currentHealth: number) => {
    dispatch({ type: 'UPDATE_BOSS_PROGRESS', payload: { id, currentHealth } });
  };

  return (
    <BossContext.Provider value={{ ...state, addBoss, updateBoss, deleteBoss, updateBossProgress }}>
      {children}
    </BossContext.Provider>
  );
};

export const useBoss = () => {
  const context = useContext(BossContext);
  if (context === undefined) {
    throw new Error('useBoss must be used within a BossProvider');
  }
  return context;
};
