import React, { useState } from 'react';
import { Trash2, Calendar, Circle, CheckCircle, Gift } from 'lucide-react';
import clsx from 'clsx';
import { Task } from '../../types';
import { useTask, useAttribute, useUser } from '../../context';
import { formatDate } from '../../utils/dateUtils';
import { ConfirmModal } from '../common/ConfirmModal';

interface TaskItemProps {
  task: Task;
  onEdit: (task: Task) => void;
}

const priorityColors = {
  low: 'bg-green-100 text-green-800',
  medium: 'bg-yellow-100 text-yellow-800',
  high: 'bg-red-100 text-red-800',
};

const priorityLabels = {
  low: '',
  medium: '',
  high: '',
};

export const TaskItem: React.FC<TaskItemProps> = ({ task, onEdit }) => {
  const { toggleTaskStatus, deleteTask } = useTask();
  const { updateAttributeValue, attributes } = useAttribute();
  const { profile, updateProfile } = useUser();
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);

  const handleToggle = (e: React.MouseEvent) => {
    e.stopPropagation();
    toggleTaskStatus(task.id);
    
    // Apply or revert rewards based on new status
    // Note: This logic assumes the toggle happens successfully. 
    // Ideally this logic should be in a service layer or context, but for now here is fine.
    // If current status is NOT completed, it will BECOME completed -> Add rewards
    // If current status IS completed, it will BECOME pending -> Subtract rewards
    
    if (task.rewards && task.rewards.length > 0) {
      const isCompleting = task.status !== 'completed';
      
      task.rewards.forEach(reward => {
        if (reward.type === 'attribute' && reward.attributeId) {
          const attribute = attributes.find(a => a.id === reward.attributeId);
          if (attribute) {
            const newValue = isCompleting 
              ? attribute.value + reward.value 
              : Math.max(0, attribute.value - reward.value);
            
            updateAttributeValue(attribute.id, newValue);
          }
        } else if (reward.type === 'money') {
          const newValue = isCompleting 
            ? profile.money + reward.value 
            : Math.max(0, profile.money - reward.value);
          
          updateProfile({ money: newValue });
        }
      });
    }
  };

  const handleDeleteClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    setIsDeleteModalOpen(true);
  };

  const handleDeleteConfirm = () => {
    deleteTask(task.id);
  };

  return (
    <>
      <div
        onClick={() => onEdit(task)}
        className={clsx(
          'flex items-start p-3 mb-2 bg-white rounded-lg shadow-sm border border-gray-100 transition-all hover:shadow-md group cursor-pointer',
          task.status === 'completed' && 'opacity-60 bg-gray-50'
        )}
      >
        <button
          onClick={handleToggle}
          className={clsx(
            'mt-0.5 mr-3 flex-shrink-0 transition-colors',
            task.status === 'completed' ? 'text-green-500' : 'text-gray-300 hover:text-gray-400'
          )}
        >
          {task.status === 'completed' ? <CheckCircle size={20} /> : <Circle size={20} />}
        </button>

        <div className="flex-1 min-w-0">
          <div className="flex items-center justify-between mb-1">
            <div className="flex items-center gap-2 min-w-0 flex-1 mr-2">
              <h3
                className={clsx(
                  'text-sm font-semibold truncate',
                  task.status === 'completed' ? 'text-gray-500 line-through' : 'text-gray-800'
                )}
              >
                {task.title}
              </h3>
              {/* Removed Edit Button */}
            </div>
            <span
              className={clsx(
                'px-3 py-1 text-sm font-medium rounded-full flex-shrink-0',
                priorityColors[task.priority]
              )}
            >
              {priorityLabels[task.priority]}
            </span>
          </div>

          {task.description && (
            <p className="text-xs text-gray-500 mb-1">{task.description}</p>
          )}

          <div className="flex flex-wrap items-center gap-2 mt-1">
            <div className="flex items-center text-[10px] text-gray-400 space-x-2">
              {task.dueDate && (
                <div className="flex items-center">
                  <Calendar size={10} className="mr-1" />
                  <span>{formatDate(task.dueDate, 'MM-dd HH:mm')}</span>
                </div>
              )}
              {/* <span>{formatDate(task.createdAt, 'MM-dd')}</span> */}
            </div>

            {task.rewards && task.rewards.length > 0 && (
              <div className="flex items-center space-x-1">
                {task.rewards.map((reward, idx) => {
                  if (reward.type === 'attribute' && reward.attributeId) {
                    const attrName = attributes.find(a => a.id === reward.attributeId)?.name;
                    if (!attrName) return null;
                    return (
                      <span key={idx} className="flex items-center text-[10px] bg-yellow-50 text-yellow-700 px-1.5 py-0.5 rounded border border-yellow-100">
                        <Gift size={8} className="mr-1" />
                        {attrName} +{reward.value}
                      </span>
                    );
                  } else if (reward.type === 'money') {
                    return (
                      <span key={idx} className="flex items-center text-[10px] bg-green-50 text-green-700 px-1.5 py-0.5 rounded border border-green-100">
                        <Gift size={8} className="mr-1" />
                         +{reward.value}
                      </span>
                    );
                  }
                  return null;
                })}
              </div>
            )}
          </div>
        </div>

        <button
          onClick={handleDeleteClick}
          className="ml-2 p-1.5 text-gray-400 hover:text-red-500 hover:bg-red-50 rounded-full transition-all"
        >
          <Trash2 size={16} />
        </button>
      </div>

      <ConfirmModal
        isOpen={isDeleteModalOpen}
        onClose={() => setIsDeleteModalOpen(false)}
        onConfirm={handleDeleteConfirm}
        title=""
        message=""
        confirmText=""
        isDanger
      />
    </>
  );
};
