import React from 'react';
import { Outlet } from 'react-router-dom';
import { BottomNavigation } from './BottomNavigation';
import { Sidebar } from './Sidebar';
import { SettingsButton } from './settings/SettingsButton';
import { useUser } from '../context';
import { BookOfAnswers } from './tools/BookOfAnswers';

export const Layout: React.FC = () => {
  // Use user context for background image
  const { profile } = useUser();

  return (
    <div 
      className="min-h-screen bg-gray-50 text-gray-900 font-sans transition-all duration-500 bg-cover bg-center bg-fixed"
      style={{
        backgroundImage: profile.backgroundImage ? `url(${profile.backgroundImage})` : undefined
      }}
    >
      {/* Overlay to ensure text readability if background is set */}
      {profile.backgroundImage && (
        <div className="fixed inset-0 bg-white/60 backdrop-blur-sm z-0 pointer-events-none" />
      )}

      <div className="relative z-10">
        <Sidebar />
        <main className="md:ml-64 pb-16 md:pb-0 min-h-screen">
          <div className="max-w-4xl mx-auto p-4 md:p-8">
            <Outlet />
          </div>
        </main>
        <BottomNavigation />
        <SettingsButton />
        <BookOfAnswers />
      </div>
    </div>
  );
};
