import React from 'react';
import { Plus, Trash2, Zap, Sword, Shield, Heart, Brain, Smile, Star, Crown, Target, Book } from 'lucide-react';
import { Reward, Attribute } from '../../types';
import { useAttribute } from '../../context';

interface RewardSelectorProps {
  rewards: Reward[];
  onChange: (rewards: Reward[]) => void;
}

const iconMap: Record<string, React.ElementType> = {
  Sword, Shield, Heart, Zap, Brain, Smile, Star, Crown, Target, Book
};

export const RewardSelector: React.FC<RewardSelectorProps> = ({ rewards, onChange }) => {
  const { attributes } = useAttribute();

  const handleAddReward = () => {
    if (attributes.length === 0) {
      onChange([...rewards, { type: 'money', value: 100 }]);
    } else {
      onChange([...rewards, { type: 'attribute', attributeId: attributes[0].id, value: 100 }]);
    }
  };

  const handleRemoveReward = (index: number) => {
    const newRewards = [...rewards];
    newRewards.splice(index, 1);
    onChange(newRewards);
  };

  const handleUpdateReward = (index: number, field: keyof Reward, value: any) => {
    const newRewards = [...rewards];
    newRewards[index] = { ...newRewards[index], [field]: value };
    if (field === 'type' && value === 'money') {
      delete newRewards[index].attributeId;
    }
    onChange(newRewards);
  };

  const renderRewardIcon = (reward: Reward) => {
    if (reward.type === 'money') {
      return <Book size={16} className="text-green-500 shrink-0" />;
    }
    
    const attribute = attributes.find(a => a.id === reward.attributeId);
    if (!attribute) return <Zap size={16} className="text-yellow-500 shrink-0" />;

    if (attribute.image?.startsWith('icon:')) {
      const iconName = attribute.image.split(':')[1];
      const IconComponent = iconMap[iconName] || Zap;
      return <IconComponent size={16} className="text-yellow-500 shrink-0" />;
    }
    
    if (attribute.image) {
      return <img src={attribute.image} alt={attribute.name} className="w-4 h-4 object-cover rounded-full shrink-0" />;
    }

    return <Zap size={16} className="text-yellow-500 shrink-0" />;
  };

  if (attributes.length === 0) {
    return (
      <div className="text-sm text-gray-500 italic">
        
      </div>
    );
  }

  return (
    <div className="space-y-2">
      <div className="flex items-center justify-between">
        <label className="block text-sm font-medium text-gray-700"> ()</label>
        <button
          type="button"
          onClick={handleAddReward}
          className="text-xs flex items-center text-blue-600 hover:text-blue-700"
        >
          <Plus size={14} className="mr-1" />
          
        </button>
      </div>

      {rewards.length === 0 && (
        <p className="text-xs text-gray-400"></p>
      )}

      <div className="space-y-2">
        {rewards.map((reward, index) => (
        <div key={index} className="flex items-center space-x-2 bg-gray-50 p-2 rounded-lg border border-gray-200">
          {renderRewardIcon(reward)}
          <select
            value={reward.type}
            onChange={(e) => handleUpdateReward(index, 'type', e.target.value)}
            className="w-24 text-sm bg-transparent border-none focus:ring-0 text-gray-700"
          >
            <option value="attribute"></option>
            <option value="money"></option>
          </select>
          {reward.type === 'attribute' && (
            <select
              value={reward.attributeId}
              onChange={(e) => handleUpdateReward(index, 'attributeId', e.target.value)}
              className="flex-1 text-sm bg-transparent border-none focus:ring-0 text-gray-700"
            >
              {attributes.map((attr) => (
                <option key={attr.id} value={attr.id}>
                  {attr.name}
                </option>
              ))}
            </select>
          )}
          {reward.type === 'money' && (
            <span className="flex-1 text-sm font-medium text-gray-700"></span>
          )}
          <span className="text-sm text-gray-500">+</span>
          <input
            type="number"
            value={reward.value}
            onChange={(e) => handleUpdateReward(index, 'value', parseInt(e.target.value) || 0)}
            className="w-16 text-sm px-2 py-1 border border-gray-300 rounded focus:outline-none focus:ring-1 focus:ring-blue-500 bg-white text-gray-900"
          />
          <button
            type="button"
            onClick={() => handleRemoveReward(index)}
            className="text-gray-400 hover:text-red-500"
          >
            <Trash2 size={14} />
          </button>
        </div>
      ))}
      </div>
    </div>
  );
};
