import React, { createContext, useContext, useEffect, useReducer } from 'react';
import { v4 as uuidv4 } from 'uuid';
import { Task } from '../types';
import { loadFromStorage, saveToStorage, STORAGE_KEYS } from '../utils/storage';
import { getCurrentISOString } from '../utils/dateUtils';

// Action Types
type TaskAction =
  | { type: 'ADD_TASK'; payload: Omit<Task, 'id' | 'createdAt' | 'updatedAt'> }
  | { type: 'UPDATE_TASK'; payload: Task }
  | { type: 'DELETE_TASK'; payload: string }
  | { type: 'TOGGLE_TASK_STATUS'; payload: string }
  | { type: 'SET_TASKS'; payload: Task[] };

// State Type
interface TaskState {
  tasks: Task[];
}

// Context Type
interface TaskContextType extends TaskState {
  addTask: (task: Omit<Task, 'id' | 'createdAt' | 'updatedAt'>) => void;
  updateTask: (task: Task) => void;
  deleteTask: (id: string) => void;
  toggleTaskStatus: (id: string) => void;
}

const TaskContext = createContext<TaskContextType | undefined>(undefined);

const taskReducer = (state: TaskState, action: TaskAction): TaskState => {
  switch (action.type) {
    case 'ADD_TASK':
      const newTask: Task = {
        ...action.payload,
        id: uuidv4(),
        createdAt: getCurrentISOString(),
        updatedAt: getCurrentISOString(),
      };
      return { tasks: [newTask, ...state.tasks] };
    case 'UPDATE_TASK':
      return {
        tasks: state.tasks.map((task) =>
          task.id === action.payload.id ? { ...action.payload, updatedAt: getCurrentISOString() } : task
        ),
      };
    case 'DELETE_TASK':
      return { tasks: state.tasks.filter((task) => task.id !== action.payload) };
    case 'TOGGLE_TASK_STATUS':
      return {
        tasks: state.tasks.map((task) => {
          if (task.id === action.payload) {
            const newStatus = task.status === 'completed' ? 'pending' : 'completed';
            return { ...task, status: newStatus, updatedAt: getCurrentISOString() };
          }
          return task;
        }),
      };
    case 'SET_TASKS':
      return { tasks: action.payload };
    default:
      return state;
  }
};

export const TaskProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [state, dispatch] = useReducer(taskReducer, {
    tasks: [],
  });

  // Load from storage on mount
  useEffect(() => {
    const storedTasks = loadFromStorage<Task[]>(STORAGE_KEYS.TASKS, []);
    dispatch({ type: 'SET_TASKS', payload: storedTasks });
  }, []);

  // Save to storage on change
  useEffect(() => {
    if (state.tasks.length > 0) { // Avoid saving empty array on initial load if not intended, but actually we want to sync
        // A better approach is to not save on the very first render before loading.
        // But useEffect runs after paint.
        // To keep it simple: we load once, then we save whenever state.tasks changes.
        // However, if we load [], and save [], it's fine.
        // The issue is if we have data in storage, init state is [], useEffect 1 runs (loads data), dispatch updates state.
        // Then useEffect 2 runs (saves data).
        // If useEffect 2 runs before useEffect 1 finishes (not possible for sync), or if initial state [] overwrites storage?
        // No, useEffect 2 dependency is state.tasks.
        // Initial render: state.tasks = []. useEffect 1 runs -> loads data -> dispatch.
        // Dispatch triggers re-render. state.tasks = [data]. useEffect 2 runs -> saves [data].
        // This is fine.
        // EXCEPT if loadFromStorage returns [] but we want to persist persistence.
        // Wait, if loadFromStorage returns valid data, we dispatch it.
        // Then useEffect 2 sees new data and saves it back. No data loss.
        // But what if we want to separate load and save?
    }
     saveToStorage(STORAGE_KEYS.TASKS, state.tasks);
  }, [state.tasks]);


  const addTask = (task: Omit<Task, 'id' | 'createdAt' | 'updatedAt'>) => {
    dispatch({ type: 'ADD_TASK', payload: task });
  };

  const updateTask = (task: Task) => {
    dispatch({ type: 'UPDATE_TASK', payload: task });
  };

  const deleteTask = (id: string) => {
    dispatch({ type: 'DELETE_TASK', payload: id });
  };

  const toggleTaskStatus = (id: string) => {
    dispatch({ type: 'TOGGLE_TASK_STATUS', payload: id });
  };

  return (
    <TaskContext.Provider value={{ ...state, addTask, updateTask, deleteTask, toggleTaskStatus }}>
      {children}
    </TaskContext.Provider>
  );
};

export const useTask = () => {
  const context = useContext(TaskContext);
  if (context === undefined) {
    throw new Error('useTask must be used within a TaskProvider');
  }
  return context;
};
