import React, { useState, useMemo, useEffect } from 'react';
import { Plus, Filter, SortDesc, Clock } from 'lucide-react';
import { useTask } from '../context';
import { TaskList } from '../components/tasks/TaskList';
import { TaskModal } from '../components/tasks/TaskModal';
import { Task } from '../types';

export const TasksPage: React.FC = () => {
  const { tasks } = useTask();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingTask, setEditingTask] = useState<Task | undefined>(undefined);
  const [filterStatus, setFilterStatus] = useState<'all' | 'active' | 'completed'>('all');
  const [sortBy, setSortBy] = useState<'createdAt' | 'priority' | 'dueDate'>('createdAt');
  const [currentTime, setCurrentTime] = useState(new Date());

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  const formatDate = (date: Date) => {
    return date.toLocaleDateString('zh-CN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      weekday: 'long',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit'
    });
  };

  const filteredTasks = useMemo(() => {
    let result = [...tasks];

    // Filter by status
    if (filterStatus === 'active') {
      result = result.filter((t) => t.status !== 'completed');
    } else if (filterStatus === 'completed') {
      result = result.filter((t) => t.status === 'completed');
    }

    // Sort
    result.sort((a, b) => {
      if (sortBy === 'priority') {
        const priorityOrder = { high: 3, medium: 2, low: 1 };
        return priorityOrder[b.priority] - priorityOrder[a.priority];
      } else if (sortBy === 'dueDate') {
        if (!a.dueDate) return 1;
        if (!b.dueDate) return -1;
        return new Date(a.dueDate).getTime() - new Date(b.dueDate).getTime();
      } else {
        // createdAt
        return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
      }
    });

    return result;
  }, [tasks, filterStatus, sortBy]);

  const handleEditTask = (task: Task) => {
    setEditingTask(task);
    setIsModalOpen(true);
  };

  const handleNewTask = () => {
    setEditingTask(undefined);
    setIsModalOpen(true);
  };

  return (
    <div className="space-y-3">
      <div className="relative flex flex-col items-center justify-center pt-2 pb-2">
        <h1 className="text-2xl font-bold text-gray-900 mb-2"></h1>
        <div className="flex items-center gap-4 w-full">
            <div className="hidden sm:flex items-center text-sm text-gray-500 bg-gray-50 px-3 py-1 rounded-full border border-gray-100">
                <Clock size={14} className="mr-1.5 text-blue-500" />
                <span className="font-medium font-mono">{formatDate(currentTime)}</span>
            </div>
            <button
              onClick={handleNewTask}
              className="flex items-center justify-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors shadow-sm w-full"
            >
              <Plus size={20} className="mr-2" />
              
            </button>
        </div>
      </div>
      
      {/* Mobile Time Display */}
      <div className="sm:hidden flex items-center justify-center text-sm text-gray-500 bg-gray-50 px-3 py-1.5 rounded-lg border border-gray-100">
          <Clock size={14} className="mr-1.5 text-blue-500" />
          <span className="font-medium font-mono">{formatDate(currentTime)}</span>
      </div>

      <div className="flex flex-col sm:flex-row gap-3 bg-white p-3 rounded-xl border border-gray-100 shadow-sm">
        <div className="flex items-center space-x-2 flex-1">
          <Filter size={18} className="text-gray-400" />
          <select
            value={filterStatus}
            onChange={(e) => setFilterStatus(e.target.value as any)}
            className="bg-transparent text-sm font-medium text-gray-600 focus:outline-none cursor-pointer w-full sm:w-auto"
          >
            <option value="all"></option>
            <option value="active"></option>
            <option value="completed"></option>
          </select>
        </div>

        <div className="flex items-center space-x-2 flex-1 sm:justify-end">
          <SortDesc size={18} className="text-gray-400" />
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value as any)}
            className="bg-transparent text-sm font-medium text-gray-600 focus:outline-none cursor-pointer w-full sm:w-auto"
          >
            <option value="createdAt"></option>
            <option value="priority"></option>
            <option value="dueDate"></option>
          </select>
        </div>
      </div>

      <TaskList tasks={filteredTasks} onEdit={handleEditTask} />

      <TaskModal 
        isOpen={isModalOpen} 
        onClose={() => setIsModalOpen(false)} 
        initialData={editingTask}
      />
    </div>
  );
};
